import { mapState,mapGetters } from 'vuex';
import 	taskArtisan from './taskArtisan.vue'
export default {
	data() {
		return {
			tab_list: [
				{
					name: '当前任务',
					id: 'dcrw',
					data: []
				},
				{
					name: '已完成',
					id: 'ywc',
					data: []
				}
			],
			current_tab: {
				id: 'dcrw',
				index: 0
			},
			loading:true
		};
	},
	onLoad() {
		
		try {
		    const value = uni.getStorageSync('user_info');
		    if (!value) {
		        console.log(value);
		    	uni.reLaunch ({
		    		url: '../../welcome/welcome'
		    	})
		    }else{
				this.init()
		    }
		} catch (e) {
		    // error
			console.log(e)
		}
	},
	components:{
		taskArtisan
	},
	computed: {
		...mapState(['has_task']),
		...mapGetters(['user_role'])
	},
	methods: {
		init(){
			Promise.all([this.get_dcrw_list(),this.get_ywc_list()]).then(values =>{
				console.log(values)
				this.tab_list[0].data = values[0].data.data
				this.tab_list[1].data = values[1].data.data
				this.loading = false
			})
		},
		get_dcrw_list(){
			return this.$http.get('personwx.projectinfolist/1.0/',{
				isFinish:'0',
			})
		},
		get_ywc_list(){
			return this.$http.get('personwx.projectinfolist/1.0/',{
				isFinish:'1',
			})
		},
		select_tab(id, index) {
			this.current_tab.id = id;
			this.current_tab.index = index;
		},
		ontabchange(event) {
			console.log(event);
			let index = event.detail.current;
			this.current_tab.index = index;
			this.current_tab.id = this.tab_list[index].id;
		},
		loadMore(index) {
			console.log(index);
		},
		go_detail(item){
			console.log(item)
			let status = this.$utils._get(item,'status','0')
			switch (status){
				case '0' || '1':
					uni.navigateTo({
						url: `/pages/tabbar/task/createTask/createTask?id=${item.id}`
					});
					break;
				case '0' || '1':
					uni.switchTab({
						url:"/pages/tabbar/workbench/workbench"
					})
					break;
				default:
					break;
			}
			
			// if(this.$utils._get(item,'status','0') == '0'){
			// 	uni.switchTab({
			// 		url:"/pages/tabbar/workbench/workbench"
			// 	})
			// }else{
			// 	uni.navigateTo({
			// 		url: '/pages/tabbar/task/taskMain/taskMain'
			// 	});
			// }
		},
		goto_add(){
			uni.navigateTo({
				url: '/pages/tabbar/task/createTask/createTask'
			});
		},
		deliveryPeriod(time){
			return this.$utils.format_date(time)
		}
	}
};
